import React, { Component } from "react";
import { MainHero, AboutMe, MyWork, Contact } from "./sections";
import { ModalManager } from "react-dynamic-modal";
import { CloseIcon } from "../../components/Icons";
import classNames from "classnames";

class Home extends Component {
  state = {
    animatedHeader: false,
    projectOpen: false,
    animateBubbles: false,
    projectFinishedTransition: false,
    projectTemplateColor: "#efefef",
    animatedElements: {
      avatar: "animated",
      title: "animated",
      description1: "animated",
      description2: "animated",
      cta: "animated",
      social: "animated",
    },
  };

  componentDidMount() {
    this.startAnimation();
  }

  startAnimation() {
    setTimeout(() => {
      this.setState({ animatedHeader: true });
    }, 150);
    setTimeout(this.sequentialAnimation, 50);
  }

  sequentialAnimation = () => {
    const { animatedElements } = this.state;

    let elements = animatedElements;
    let elementKeys = Object.keys(animatedElements);

    // MUST MATCH CSS transition duration to make them sync
    const animationDuration = 100;

    elementKeys.map((key, index) => {
      setTimeout(() => {
        elements[key] = `${animatedElements[key]} animating`;
        this.setState({ animatedElements: elements });
      }, animationDuration * (index + 1));
      return false;
    });
  };

  closeModal = () => {
    const { mobile } = this.props;

    if (mobile) {
      this.setState(
        { projectOpen: false, projectFinishedTransition: false },
        () => {
          document.documentElement.scrollTop = this.state.scrollTop;
          document.body.scrollTop = this.state.scrollTop;
        }
      );

      this.props.onProjectClose();
      this.props.history.push("/");
    }

    ModalManager.close();
  };

  render() {
    const { animatedElements, animatedHeader } = this.state;
    const {
      availableProjects,
      projects,
      mobile,
      maxProjectsLength,
      onShowMoreProjects,
      history,
    } = this.props;

    return (
      <div
        className={classNames({
          "project-open": this.state.projectOpen && mobile,
          "project-animated": this.state.projectFinishedTransition && mobile,
        })}
      >
        <MainHero
          animatedHeader={animatedHeader}
          animatedElements={animatedElements}
        />

        <MyWork
          history={history}
          mobile={mobile}
          availableProjects={availableProjects}
          projects={projects}
          onShowMoreProjects={onShowMoreProjects}
          maxProjectsLength={maxProjectsLength}
          onProjectClose={() => this.closeModal()}
          onProjectOpen={(templateColor, scrollTop) => {
            if (mobile) {
              this.setState({
                projectOpen: true,
                projectTemplateColor: templateColor,
                scrollTop: scrollTop,
              });

              this.props.onProjectOpen();

              setTimeout(() => {
                this.setState({ projectFinishedTransition: true });
              }, 400);
            }
          }}
        />

        <AboutMe animate={this.state.animateBubbles} mobile={mobile} />

        <Contact />

        {mobile && this.state.projectOpen && (
          <button
            className="modal-close-btn pull-right mobile-close"
            style={{ backgroundColor: this.state.projectTemplateColor }}
            onClick={() => this.closeModal()}
          >
            <CloseIcon />
          </button>
        )}
      </div>
    );
  }
}

export default Home;



