import React, { Component } from "react";
import config from "../../../config";

class Contact extends Component {
  state = {
    email: "",
    name: "",
    message: "",
    sending: false,
    valid: true,
    sent: false,
  };

  validateEmail = (email) => {
    // eslint-disable-next-line
    var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(email.toLowerCase());
  };

  validate = () => {
    if (!this.state.email.trim().length) {
      return false;
    }

    if (!this.validateEmail(this.state.email)) {
      return false;
    }

    if (!this.state.name.trim().length) {
      return false;
    }

    if (!this.state.message.trim().length) {
      return false;
    }

    return true;
  };

  sendMessage = () => {
    const { email, message, name } = this.state;

    if (this.validate()) {
      this.setState({ sending: true, valid: true });

      fetch(`${config.api}/contactMessage`, {
        headers: {
          "Content-Type": "application/json",
        },
        method: "post",
        body: JSON.stringify({
          email,
          message,
          name,
        }),
      })
        .then((data) => {
          if (data.status === 400) {
            this.setState({ sending: false, sent: false });
            return;
          }

          this.setState({ sending: false, sent: true });
        })
        .catch((err) => {
          console.error(err);
          this.setState({ sending: false });
        });
    } else {
      this.setState({ valid: false });
    }
  };

  render() {
    return (
      <section id="contact" className="page-section col-md-12 dark-grey">
        <section className="container">
          <h3 className="page-title"> CONTACT </h3>
          <h2 className="page-subtitle"> Get in touch </h2>

          <div className="col-md-12 form-group">
            <form
              onSubmit={(e) => {
                e.preventDefault();
                this.sendMessage();
              }}
              className={!this.state.valid ? "not-valid" : ""}
            >
              <input
                onChange={(e) => this.setState({ email: e.target.value })}
                value={this.state.email}
                name="email"
                className="form-control"
                type="email"
                placeholder="Enter your email..."
              />
              <input
                onChange={(e) => this.setState({ name: e.target.value })}
                value={this.state.name}
                name="fullname"
                className="form-control"
                type="text"
                placeholder="Enter your full name..."
              />
              <textarea
                name="message"
                className="form-control form-textarea"
                onChange={(e) => this.setState({ message: e.target.value })}
                value={this.state.message}
                placeholder="Enter your message..."
              ></textarea>

              <button
                type="submit"
                id="sendButton"
                className="btn send-button"
                disabled={this.state.sent}
              >
                <span
                  style={{ display: "none" }}
                  className="glyphicon glyphicon-repeat"
                ></span>
                <span
                  style={{ display: this.state.sent ? "inline-block" : "none" }}
                  className="glyphicon glyphicon-ok"
                ></span>
                <span className="formLoader loader-inner ball-clip-rotate"></span>
                <span
                  style={{ display: this.state.sent ? "none" : "inline-block" }}
                  className="glyphicon glyphicon-send send-icon"
                ></span>
                &nbsp;
                <span className="button-text">
                  {!this.state.sent && !this.state.sending && "Send"}

                  {this.state.sending && "Sending ..."}

                  {this.state.sent && "Sent"}
                </span>
              </button>
            </form>
          </div>
        </section>
      </section>
    );
  }
}

export default Contact;



