import React, { Component } from "react";
import { Nav, NavItem, NavLink, Card, CardImg } from "reactstrap";
import { Modal, ModalManager } from "react-dynamic-modal";
import { CloseIcon, ExternalLink } from "../Icons";
import { preloadImage } from "../../utils";
import { CSSTransition, TransitionGroup } from "react-transition-group";
import ImageZoom from "react-medium-image-zoom";
import { DefaultPlayer as Video } from "react-html5video";
import Waypoint from "react-waypoint";
import "./projects-grid.css";

const ProjectVideoPlayer = ({ poster = "", src = "", autoplay = false }) => (
  <Video
    autoPlay={autoplay}
    muted
    controls={["PlayPause", "Seek", "Time"]}
    poster={poster}
    onCanPlayThrough={() => {
      // Do stuff
    }}
  >
    <source src={src} type="video/mp4" />
  </Video>
);

const Fade = ({ children, ...props }) => (
  <CSSTransition {...props} timeout={200} classNames="example">
    {children}
  </CSSTransition>
);

const modalMobile = {
  overlay: {
    width: "100%",
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    zIndex: 997,
    overflow: "hidden",
    backgroundColor: "rgba(255, 255, 255, 0.7)",
    perspective: "none",
  },

  content: {
    position: "relative",
    margin: "0",
    width: "100%",
    border: "0",
    background: "#fff",
    overflow: "auto",
    overflowScrolling: "touch",
    WebkitOverflowScrolling: "touch",
    borderRadius: "0px",
    outline: "none",
    transform: "translateZ(0px) translate3d(0,0,0)",
    perspective: "none",
  },
};

const modalStyles = {
  overlay: {
    position: "fixed",
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    zIndex: 997,
    overflow: "auto",
    overflowScrolling: "touch",
    WebkitOverflowScrolling: "touch",
    perspective: "none",
    backgroundColor: "rgba(255, 255, 255, 0.7)",
  },

  content: {
    position: "relative",
    margin: "5% auto",
    maxWidth: "700px",
    border: "0",
    background: "#fff",
    overflow: "auto",
    borderRadius: "0px",
    outline: "none",
    boxShadow: "0 5px 10px rgba(0, 0, 0, .6)",
    transform: "translateZ(0px) translate3d(0,0,0)",
    perspective: "none",
  },
};

const ScaleUpTransition = {
  transition: {
    property: "all",
    duration: 200,
    timingfunction: "ease-in-out",
  },
  begin: {
    transform: "scale(0.7)",
    opacity: 0,
  },
  end: {
    transform: "scale(1)",
    opacity: 1,
  },
};

class MyModal extends Component {
  imagesLoaded = 0;

  state = {
    transitioned: false,
    projectHeight: 1500,
  };

  freezeScroll = (e) => e.preventDefault();

  componentDidMount() {
    const { mobile } = this.props;

    if (!mobile) {
      document.body.style.overflow = "hidden";
    } else {
      setTimeout(() => {
        this.setState({
          transitioned: true,
          projectHeight: this.contentRef.getBoundingClientRect().height,
        });
        document.body.scrollTop = 0;

        // document.body.style.overflow = "hidden";
        // document.body.addEventListener("touchmove", this.freezeScroll, false);
        // document.body.style.position = "fixed";
        // document.body.style.height = "100%";
        // document.documentElement.style.position = "relative";
        // document.documentElement.style.overflow = "hidden";
        // document.documentElement.style.height = "100%";
      }, 400);
    }
  }

  updateHeight = () => {
    this.setState({
      projectHeight: this.contentRef.getBoundingClientRect().height,
    });
  };

  headerImgLoaded = () => {
    if (this.props.mobile) {
      this.imagesLoaded += 1;
      const height = this.contentRef.getBoundingClientRect().height;

      if (height > this.state.projectHeight) {
        this.updateHeight();
      }
    }
  };

  projectImgLoaded = () => {
    if (this.props.mobile) {
      this.imagesLoaded += 1;

      if (this.imagesLoaded === this.props.data.pictures.length + 1) {
        this.updateHeight();
      }
    }
  };

  componentWillUnmount() {
    const { mobile } = this.props;

    if (!mobile) {
      document.body.style.overflow = "visible";
    }
  }

  getExtra = (extra) => {
    if (!!extra) {
      switch (extra) {
        case "tv":
          return (
            <span
              style={{
                background: "#f5f5f5",
                padding: "5px 10px",
                borderRadius: "3px",
                color: "#8c8c8c",
              }}
            >
              Built while working for{" "}
              <a
                style={{ color: this.props.data.templateColor }}
                target="_blank"
                rel="noopener noreferrer"
                href="http://talentvillage.com"
              >
                Talent Village
              </a>
            </span>
          );
        case "adj":
          return (
            <span
              style={{
                background: "#f5f5f5",
                padding: "5px 10px",
                borderRadius: "3px",
                color: "#8c8c8c",
              }}
            >
              Built while working for{" "}
              <a
                style={{ color: this.props.data.templateColor }}
                target="_blank"
                rel="noopener noreferrer"
                href="http://adjustyourset.com"
              >
                Adjust Your Set
              </a>
            </span>
          );
        default:
          return (
            <span
              style={{
                background: this.props.data.templateColor,
                letterSpacing: 0.5,
                color: "#fff",
                padding: "5px 10px",
                borderRadius: "3px",
              }}
            >
              {extra}
            </span>
          );
      }
    }

    return null;
  };

  render() {
    const { data, onRequestClose, mobile } = this.props;
    const {
      title,
      subtitle = "Project subtitle",
      externalLink,
      templateColor,
      headerImg,
      pictures,
      content,
      video,
      category,
      extra,
    } = data;

    const extraInfo = this.getExtra(extra);

    const mobileStyled = {
      ...modalMobile,
      overlay: {
        ...modalMobile.overlay,
        position: !this.state.transitioned ? "fixed" : "absolute",
        height: this.state.projectHeight,
      },
    };

    return (
      <Modal
        style={!mobile ? modalStyles : mobileStyled}
        onRequestClose={onRequestClose}
        effect={ScaleUpTransition}
      >
        <div className="modal-content" ref={(ref) => (this.contentRef = ref)}>
          <div
            className="modal-image-container"
            style={{
              backgroundColor: templateColor,
            }}
          >
            <img
              alt=""
              className="modal-image"
              src={`/${headerImg}`}
              onLoad={this.headerImgLoaded}
            />
          </div>
          <div className="modal-info">
            <div className="row">
              <div className="col-md-6">
                <h3 className="modal-title">{title}</h3>
                <h4 className="modal-subtitle">{subtitle}</h4>

                <h5 className="built-for">{!!extra && extraInfo}</h5>
              </div>

              <div className="col-md-6">
                <div className="pull-right">
                  {pictures.map((picture, index) => (
                    <ImageZoom
                      key={index}
                      image={{
                        src: "/" + picture.src,
                        alt: "",
                        style: {
                          width: mobile ? "100%" : "100px",
                        },
                        className: "modal-project-galery-img",
                        onLoad: this.projectImgLoaded,
                      }}
                      shouldRespectMaxDimension={true}
                      zoomMargin={0}
                    />
                  ))}
                </div>
              </div>
            </div>

            <hr />

            <div className="modal-description">
              <div className="row">
                {content.map((contentItem, index) => (
                  <div key={index} className="col-sm-4 circleThumbContainer">
                    <div
                      className="circleThumb text-center"
                      style={{ backgroundColor: templateColor }}
                    >
                      <img
                        alt=""
                        className="circleImg"
                        src={"/" + contentItem.thumbnail}
                      />
                    </div>
                    <div className="description">
                      <h4 style={{ color: templateColor }}>
                        {contentItem.header}
                      </h4>
                      <p>{contentItem.description}</p>
                    </div>
                  </div>
                ))}
              </div>

              <br />

              {video && (
                <div className="row">
                  <div className="description">
                    <h4 style={{ color: templateColor, textAlign: "center" }}>
                      {" "}
                      Video{" "}
                    </h4>
                  </div>

                  <br />

                  <ProjectVideoPlayer
                    poster={video.split(".")[0] + ".jpg"}
                    src={video}
                  />
                </div>
              )}
            </div>

            <div className="row modal-bottom">
              <div className="col-md-6 col-xs-6">
                {category !== "Games" ? (
                  <a
                    className="btn btn-primary modal-view-site"
                    href={"http://" + externalLink}
                    style={{ background: templateColor }}
                    target="_blank"
                  >
                    <ExternalLink /> View Site
                  </a>
                ) : (
                  <br />
                )}
              </div>
              <div className="col-md-6 col-xs-6">
                {!this.props.mobile && (
                  <button
                    className="modal-close-btn pull-right"
                    onClick={ModalManager.close}
                  >
                    <CloseIcon />
                  </button>
                )}
              </div>
            </div>
          </div>
        </div>
      </Modal>
    );
  }
}

class ProjectElement extends Component {
  modal = false;

  componentDidMount() {
    let location = this.props.history.location.pathname;

    if (location.indexOf("/projects/") > -1) {
      let id = location.split("/projects/")[1];
      id = !!id ? parseInt(id, 10) : null;

      if (id && id === this.props.id) {
        this.openModal();
      }
    }
  }

  componentWillReceiveProps(nextProps) {
    let location = nextProps.history.location.pathname;

    if (location.indexOf("/projects/") > -1) {
      let id = location.split("/projects/")[1];

      id = !!id ? parseInt(id, 10) : null;

      if (id && id === nextProps.id && !this.modal) {
        this.openModal(true);
      }
    } else {
      if ((location === "/" || location === "") && this.modal) {
        this.modal = false;
        ModalManager.close();
        this.props.onProjectClose();
      }
    }
  }

  openModal = (noPush = false) => {
    const scrollTop =
      (window.pageYOffset || document.documentElement.scrollTop) -
      (document.documentElement.clientTop || 0);

    this.props.onProjectOpen(this.props.templateColor, scrollTop);

    const { id } = this.props;

    if (!noPush) {
      this.props.history.push(`/projects/${id}`);
    }

    ModalManager.open(
      <MyModal
        mobile={this.props.mobile}
        data={this.props}
        scrollTop={scrollTop}
        onRequestClose={() => {
          this.modal = false;
          this.props.history.push(`/`);
          this.props.onProjectClose();
        }}
      />
    );

    this.modal = true;
  };

  renderWithInview = () => {
    const { imgSource, onHover, title, onInview } = this.props;

    return (
      <Waypoint bottomOffset="50%" onEnter={() => onInview()}>
        <div
          className="col-md-4 col-xs-12 project-element in-view"
          onClick={() => this.openModal()}
          onMouseEnter={onHover}
        >
          <Card style={{ width: "100%", overflow: "hidden" }}>
            <div style={{ overflow: "hidden" }}>
              <CardImg
                top
                width="100%"
                src={`/${imgSource}`}
                alt="Card image cap"
              />
            </div>

            <div className="card-overlay">
              <div className="card-title">
                <b>{title}</b>
              </div>
            </div>
          </Card>
        </div>
      </Waypoint>
    );
  };

  renderWithoutInview = () => {
    const { imgSource, onHover, title } = this.props;

    return (
      <div
        className="col-md-4 col-xs-12 project-element in-view"
        onClick={() => this.openModal()}
        onMouseEnter={onHover}
      >
        <Card style={{ width: "100%", overflow: "hidden" }}>
          <div style={{ overflow: "hidden" }}>
            <CardImg
              top
              width="100%"
              src={`/${imgSource}`}
              alt="Card image cap"
            />
          </div>

          <div className="card-overlay">
            <div className="card-title">
              <b>{title}</b>
            </div>
          </div>
        </Card>
      </div>
    );
  };

  render() {
    const { mobile } = this.props;
    return !mobile ? this.renderWithoutInview() : this.renderWithInview();
  }
}

class ProjectsGrid extends Component {
  preloadedProjects = [];

  state = {
    filter: {},
  };

  setFilterAll = () => {
    this.setState({
      filter: {},
    });
  };

  setFilterWeb = () => {
    this.setState({
      filter: {
        category: "Web",
      },
    });
  };

  setFilterGames = () => {
    this.setState({
      filter: {
        category: "Games",
      },
    });
  };

  preloadProject = (project, inview) => {
    if (this.preloadedProjects.findIndex((p) => p.id === project.id) === -1) {
      preloadImage("/" + project.headerImg);

      // project.pictures.map(img => preloadImage(img.src));

      this.preloadedProjects.push({ id: project.id });
    }
  };

  render() {
    const { filter } = this.state;
    const { category } = filter;
    const {
      allItems = [],
      items = [],
      mobile,
      onProjectOpen,
      maxProjectsLength,
      onShowMoreProjects,
      history,
      onProjectClose,
    } = this.props;

    const filteredProjects = allItems.filter(
      (project) => project.category === filter.category
    );
    const projects = filteredProjects.length ? filteredProjects : items;

    return (
      <div>
        <Nav className="grid-nav-filter">
          <NavItem className={!category ? "active" : ""}>
            <NavLink onClick={this.setFilterAll}>All</NavLink>
          </NavItem>
          <NavItem className={category === "Web" ? "active" : ""}>
            <NavLink onClick={this.setFilterWeb}>Web Development</NavLink>
          </NavItem>
          <NavItem className={category === "Games" ? "active" : ""}>
            <NavLink onClick={this.setFilterGames}>Games Development</NavLink>
          </NavItem>
        </Nav>

        <div className="container">
          <div className="row">
            <TransitionGroup className="project-grid-list">
              {category
                ? projects
                    .filter((project) =>
                      !!category
                        ? project.category === filter.category
                        : project
                    )
                    .map((project, index) => (
                      <Fade key={index}>
                        <ProjectElement
                          mobile={mobile}
                          onProjectOpen={onProjectOpen}
                          onHover={() => this.preloadProject(project)}
                          onInview={() =>
                            this.preloadProject(project, "inview")
                          }
                          onProjectClose={onProjectClose}
                          history={history}
                          {...project}
                        />
                      </Fade>
                    ))
                : projects.map((project, index) => (
                    <Fade key={index}>
                      <ProjectElement
                        mobile={mobile}
                        onProjectOpen={onProjectOpen}
                        onHover={() => this.preloadProject(project)}
                        onInview={() => this.preloadProject(project, "inview")}
                        history={history}
                        onProjectClose={onProjectClose}
                        {...project}
                      />
                    </Fade>
                  ))}
            </TransitionGroup>
          </div>

          {projects.length < maxProjectsLength && !this.state.filter.category && (
            <button
              className="btn btn-primary see-more-button"
              style={{
                margin: "30px auto 15px auto",
                padding: "10px 50px",
                display: "block",
              }}
              onClick={onShowMoreProjects}
            >
              {" "}
              See More{" "}
            </button>
          )}
        </div>
      </div>
    );
  }
}

export default ProjectsGrid;



