import React, { Component } from "react";
import { Motion, spring } from "react-motion";

const scalableFontSize = (width, p) => {
  return (width * p) / 100;
};
const getCircleSplit = (ammount, radius) => {
  const circleSplit = [];
  const circleAngle = 360;
  const movingAngle = circleAngle / ammount;

  for (let i = 1; i <= ammount; i++) {
    circleSplit.push(movingAngle * i);
  }

  return circleSplit;
};

class Bubble extends Component {
  animation = {
    animationIndex: 0,
    animationStates: [
      {
        x: 5,
        y: -5,
      },
      {
        x: 5,
        y: -2.5,
      },
      {
        x: 5,
        y: 0,
      },
      {
        x: 5,
        y: 2.5,
      },
      {
        x: 5,
        y: 5,
      },
      {
        x: 2.5,
        y: 5,
      },
      {
        x: 0,
        y: 5,
      },
      {
        x: -2.5,
        y: 5,
      },
      {
        x: -5,
        y: 5,
      },
      {
        x: -5,
        y: -2.5,
      },
      {
        x: -5,
        y: -5,
      },
      {
        x: -2.5,
        y: -5,
      },
      {
        x: 5,
        y: -5,
      },
    ],
  };

  state = {
    x: 0,
    y: 0,
  };

  componentDidMount() {
    const randomNum = Math.random(0, 1) * 1000;

    setTimeout(() => {
      setInterval(this.animationUpdate, 90 - randomNum / 40);
    }, 100 * this.props.index + randomNum);
  }

  animationUpdate = () => {
    let { animationIndex, animationStates } = this.animation;

    if (animationIndex < animationStates.length) {
      this.setState(animationStates[animationIndex]);
      this.animation.animationIndex++;
    } else {
      this.animation.animationIndex = 0;
      this.setState(animationStates[animationIndex]);
    }
  };

  render() {
    const { size, label, radius, angle, backgroundColor } = this.props;

    const transform = !!radius
      ? "rotate(" +
        angle +
        "deg) translate(" +
        radius +
        "px) rotate(" +
        -angle +
        "deg)"
      : null;

    const style = {
      circleContainer: {
        position: "absolute",
        transform: transform,
        width: size + 2.5,
        height: size + 2.5,
      },
      circle: {
        position: "absolute",
        width: size + 2.5,
        height: size + 2.5,
        color: "#fff",
        backgroundColor: backgroundColor,
        borderRadius: size,
      },
      label: {
        position: "absolute",
        fontFamily: "sans-serif",
        fontWeight: 100,
        fontSize: scalableFontSize(size - 5, 17),
        top: "50%",
        left: "50%",
        textAlign: "center",
        transform: "translate(-50%, -50%)",
      },
    };
    return (
      <div style={style.circleContainer}>
        <Motion
          defaultStyle={{ x: 0, y: 0 }}
          style={{ x: spring(this.state.x), y: spring(this.state.y) }}
        >
          {({ x, y }) => (
            <div
              style={{
                transform: !!radius
                  ? "translate(" + x + "px, " + y + "px)"
                  : "",
                ...style.circle,
              }}
            >
              <label style={style.label}> {label} </label>
            </div>
          )}
        </Motion>
      </div>
    );
  }
}

const CoreBubble = (props) => {
  const { bubbleSize, backgroundColor } = props;
  const style = {
    position: "absolute",
    width: bubbleSize,
    height: bubbleSize,
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
  };

  return (
    <div style={style}>
      <Bubble backgroundColor={backgroundColor} size={bubbleSize} {...props} />
    </div>
  );
};

const AroundBubbles = (props) => {
  const { bubbles, bubbleSize, radius } = props;

  const style = {
    position: "absolute",
    width: bubbleSize,
    height: bubbleSize,
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
  };

  const circleSplit = getCircleSplit(bubbles.length, radius);

  return (
    <div style={style}>
      {bubbles.map((bubble, index) => (
        <Bubble
          key={index}
          index={index}
          angle={circleSplit[index]}
          radius={radius}
          size={bubbleSize}
          {...bubble}
        />
      ))}
    </div>
  );
};

class FloatingBubbles extends Component {
  coreBubbleSize = 180;
  aroundBubblesSize = 90;
  radius = 220;

  render() {
    const {
      coreBubble,
      bubbles,
      width,
      height,
      className,
      coreBubbleColor,
      aroundBubblesColor,
      mobile,
    } = this.props;

    let coreBubbleSize = 200,
      aroundBubblesSize = 100,
      radius = 220;

    if (mobile) {
      coreBubbleSize = this.coreBubbleSize / 2;
      aroundBubblesSize = this.aroundBubblesSize / 2 + 10;
      radius = this.radius / 2 + 10;
    }

    const style = {
      canvas: {
        float: "left",
        position: "relative",
        margin: "30px 0",
        width: !mobile ? width : "100%",
        height: !mobile ? height : height / 2,
      },
      bubbleContainer: {
        width: !mobile ? width : "100%",
        height: !mobile ? height : height / 2,
        position: "absolute",
        top: "50%",
        left: "50%",
        transform: "translate(-50%, -50%)",
      },
    };

    return (
      <div style={style.canvas} className={className}>
        <div style={style.bubbleContainer}>
          <CoreBubble
            bubbleSize={coreBubbleSize}
            backgroundColor={coreBubbleColor}
            {...coreBubble}
          />
          <AroundBubbles
            radius={radius}
            bubbleSize={aroundBubblesSize}
            bubbles={bubbles}
            backgroundColor={aroundBubblesColor}
          />
        </div>
      </div>
    );
  }
}

export default FloatingBubbles;



