import React from "react";
import FloatingBubbles from "../components/FloatingBubbles";

const store = {
  coreBubble: {
    label: "Experience",
    backgroundColor: "#3f464e",
  },
  bubbles: [
    {
      label: "HTML",
      backgroundColor: "#426284",
    },
    {
      label: "C#",
      backgroundColor: "#426284",
    },
    {
      label: "Unity3D",
      backgroundColor: "#426284",
    },
    {
      label: "JS",
      backgroundColor: "#426284",
    },
    {
      label: "CSS",
      backgroundColor: "#426284",
    },
    {
      label: "Node",
      backgroundColor: "#426284",
    },
    {
      label: "React",
      backgroundColor: "#426284",
    },
    {
      label: "Backbone",
      backgroundColor: "#426284",
    },
    {
      label: "React Native",
      backgroundColor: "#426284",
    },
    {
      label: "Docker",
      backgroundColor: "#426284",
    },
    {
      label: "Express",
      backgroundColor: "#426284",
    },
  ],
};

export const WebBubble = (props) => {
  return (
    <FloatingBubbles
      width="100%"
      height={600}
      coreBubble={store.coreBubble}
      bubbles={store.bubbles}
      className="web-diagram"
      coreBubbleColor="#426284"
      aroundBubblesColor="#efefef"
      mobile={props.mobile}
      animate={props.animate}
    />
  );
};

export const GamesBubble = (props) => {
  return (
    <FloatingBubbles
      width="100%"
      height={600}
      coreBubble={store.coreBubble}
      bubbles={store.bubbles}
      className="web-diagram"
      coreBubbleColor="#426284"
      aroundBubblesColor="#efefef"
    />
  );
};



