import React from "react";
import {
  GithubIcon,
  LinkedIcon,
  TwitterIcon,
  YoutubeIcon,
} from "../../../components/Icons";
import ParticleNetwork from "../../../components/ParticleNetwork";

export default (props) => {
  const { animatedHeader } = props;
  const { avatar, title, description1, description2, cta, social } =
    props.animatedElements;

  return (
    <section id="home" className="main-page col-md-12">
      <ParticleNetwork />
      <section
        id="mainHeader"
        className={`mainHeader translated ${
          animatedHeader ? "translating" : ""
        }`}
      >
        <img
          alt="avatar"
          id="avatar"
          src="/images/av.jpg"
          className={`avatar ${avatar}`}
        />
        <p id="title" className={`title ${title}`}>
          CREATIVE DEVELOPER
        </p>
        <span className="description">
          <p id="description1" className={`description1 ${description1}`}>
            I'm <span className="underlined">Adrian</span>, Senior Software
            Engineer
            <br /> with over 10 years of experience across
            <span className="underlined">
              {" "}
              Web, Mobile and Games Development.{" "}
            </span>
          </p>
          <p id="description2" className={`description2 ${description2}`}>
            I design and develop clean interfaces and scalable products.
          </p>
          <p id="cv-download" className={`cta ${cta}`}>
            <a target="_blank" href="/cv.pdf">
              <button id="cv-button" className="button">
                Download CV
              </button>
            </a>
          </p>
          <div className="social-icons">
            <div id="social" className={`social ${social}`}>
              <a href="https://twitter.com/adrianbindiu95">
                <TwitterIcon size={60} color="#fff" className="social-media" />
              </a>
              <a href="https://www.youtube.com/user/lopadi9">
                <YoutubeIcon size={60} color="#fff" className="social-media" />
              </a>
              <a href="https://github.com/Snowsoul">
                <GithubIcon size={60} color="#fff" className="social-media" />
              </a>
              <a href="https://www.linkedin.com/in/adrianbindiu95">
                <LinkedIcon size={60} color="#fff" className="social-media" />
              </a>
            </div>
          </div>
        </span>
      </section>
    </section>
  );
};



