import React, { Component } from "react";
import ParticlesJS from "particlesjs";

class ParticleNetwork extends Component {
  componentDidMount() {
    this.initParticles();
  }

  initParticles() {
    // Particles.js - https://marcbruederlin.github.io/particles.js/

    ParticlesJS.init({
      selector: "#particle-network",
      maxParticles: 190,
      color: "#2b3d53",
      connectParticles: true,
      speed: 0.3,
      sizeVariations: 4,
      minDistance: 120,

      // options for breakpoints
      responsive: [
        {
          breakpoint: 768,
          options: {
            maxParticles: 150,
            connectParticles: false,
          },
        },
        {
          breakpoint: 425,
          options: {
            maxParticles: 50,
            connectParticles: false,
          },
        },
        {
          breakpoint: 320,
          options: {
            maxParticles: 0, // disables particles.js
          },
        },
      ],
    });
  }

  render = () => <canvas id="particle-network" />;
}

export default ParticleNetwork;



