import React, { Component } from "react";
import Transition from "react-transition-group/Transition";
import scrollToElement from "scroll-to-element";

const defaultLineTransition = (duration) => ({
  transition: `transform ${duration}ms ease-in-out, opacity ${duration}ms ease-in-out`,
});

const defaultMenuTransition = (duration) => ({
  transition: `width ${duration}ms ease-in-out, height ${duration}ms ease-in-out, background ${duration}ms ease-in-out`,
});

const defaultMenuItemsTransition = (duration) => ({
  transition: `opacity ${duration}ms ease-in-out`,
});

// Menu Default values
const barDefault = { transform: "translateY(0px) rotateZ(0deg)" };

// Menu container + Items Transition
const menuTransition = { width: 170, height: 300 };
const menuItemsDefault = { display: "none", opacity: 0 };
const menuItemsTransition = { display: "block", opacity: 1 };

// Hamburger Line Transitions
const topbarTransition = { transform: "translateY(4px) rotateZ(45deg)" };
const middlebarTransition = { transform: "translateX(-50px)", opacity: 0 };
const bottombarTransition = { transform: "translateY(-10px) rotateZ(-45deg)" };

const defaultStyle = (duration, defaultTransition, defaultValue) => ({
  ...defaultTransition(duration),
  ...defaultValue,
});

const transitionStyles = (transition) => {
  return {
    entering: transition,
    entered: transition,
  };
};

const ToggleTransition = ({
  duration,
  toggle,
  transition,
  defaultTransition,
  defaultValue,
  children,
  className,
}) => {
  return (
    <Transition in={toggle} timeout={duration}>
      {(state) => (
        <div
          className={className}
          style={{
            ...defaultStyle(duration, defaultTransition),
            ...transitionStyles(transition)[state],
          }}
        >
          {children}
        </div>
      )}
    </Transition>
  );
};

class HamburgerMenu extends Component {
  state = {
    menuToggle: false,
  };

  toggleMenu = () => {
    this.setState({ menuToggle: !this.state.menuToggle });
  };

  scrollTo = (id) => {
    scrollToElement(id, {
      offset: 0,
      ease: "linear",
      duration: 200,
    });
  };

  render() {
    const { show = true } = this.props;
    const { menuToggle } = this.state;

    return (
      <div style={{ display: show ? "block" : "none" }}>
        <ToggleTransition
          defaultTransition={defaultMenuTransition}
          transition={menuTransition}
          toggle={menuToggle}
          className={!menuToggle ? "menu-bar" : "menu-bar anim-toggled"}
          duration={200}
        >
          <section id="menu"></section>
        </ToggleTransition>

        <div onClick={this.toggleMenu} id="menu-icon" className="menu-icon">
          <ToggleTransition
            defaultValue={barDefault}
            duration={200}
            defaultTransition={defaultLineTransition}
            transition={topbarTransition}
            toggle={menuToggle}
          >
            <div id="top-bar" className="bar"></div>
          </ToggleTransition>
          <ToggleTransition
            defaultValue={barDefault}
            duration={100}
            defaultTransition={defaultLineTransition}
            transition={middlebarTransition}
            toggle={menuToggle}
          >
            <div id="middle-bar" className="bar"></div>
          </ToggleTransition>
          <ToggleTransition
            defaultValue={barDefault}
            duration={200}
            defaultTransition={defaultLineTransition}
            transition={bottombarTransition}
            toggle={menuToggle}
          >
            <div id="bottom-bar" className="bar"></div>
          </ToggleTransition>
        </div>

        <div style={{ visibility: menuToggle ? "visible" : "hidden" }}>
          <ToggleTransition
            defaultValue={menuItemsDefault}
            duration={300}
            defaultTransition={defaultMenuItemsTransition}
            transition={menuItemsTransition}
            toggle={this.state.menuToggle}
            className="menu-content"
          >
            <div id="menu-content">
              <div className="menu-items">
                <span
                  className="scroll"
                  onClick={() => {
                    this.scrollTo("#home");
                  }}
                >
                  Home
                </span>
                <span
                  className="scroll"
                  onClick={() => {
                    this.scrollTo("#work");
                  }}
                >
                  Work
                </span>
                <span
                  className="scroll"
                  onClick={() => {
                    this.scrollTo("#about");
                  }}
                >
                  About
                </span>
                <span
                  className="scroll"
                  onClick={() => {
                    this.scrollTo("#contact");
                  }}
                >
                  Contact
                </span>
              </div>
            </div>
          </ToggleTransition>
        </div>
      </div>
    );
  }
}

export default HamburgerMenu;



