import React, { Component } from "react";
import HamburgerMenu from "./components/HamburgerMenu";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Home from "./pages/Home/Home";
import "bootstrap/dist/css/bootstrap.min.css";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "./App.css";
import config from "./config";
import jsonData from "./data/data.json";

class App extends Component {
  state = {
    mobile: false,
    projectOpen: false,
    showingProjectsCount: 9,
    availableProjects: [],
    projects: [],
  };

  componentWillMount() {
    this.onWindowResize();
  }

  componentDidMount() {
    window.onresize = this.onWindowResize;
    this.fetchProjects();
  }

  getProjects = (projects) => {
    let newProjects = [...projects];
    newProjects.length = this.state.showingProjectsCount;

    return newProjects;
  };

  fetchProjects() {
    // If project is offline, only use the json static data file
    if (!!config.offline) {
      const { projects } = jsonData;
      this.setState({
        availableProjects: projects,
        projects: this.getProjects(projects),
      });

      return;
    }

    fetch(config.api + "/projects", {
      mode: "cors",
    })
      .then((data) => data.json())
      .then(({ projects }) =>
        this.setState({
          availableProjects: projects,
          projects: this.getProjects(projects),
        })
      )
      .catch((err) => console.error(err));
  }

  onWindowResize = (event) => {
    if (document.body.clientWidth <= 970 && !this.state.mobile) {
      this.setState({ mobile: true });
    } else {
      if (this.state.mobile && document.body.clientWidth >= 971) {
        this.setState({ mobile: false });
      }
    }
  };

  onProjectOpen = () => {
    this.setState({ projectOpen: true });
  };

  onProjectClose = () => {
    this.setState({ projectOpen: false });
  };

  onShowMoreProjects = () => {
    let newProjectsLength = 9;
    let newProjects = [...this.state.availableProjects];

    const showingProjectsLength = this.state.projects.length;
    const availableProjectsLength = this.state.availableProjects.length;

    if (showingProjectsLength < availableProjectsLength) {
      if (availableProjectsLength - showingProjectsLength < 9) {
        newProjectsLength = availableProjectsLength;
      } else {
        newProjectsLength = showingProjectsLength + 9;
      }

      newProjects.length = newProjectsLength;

      this.setState({
        projects: newProjects,
        showingProjectsCount: newProjectsLength,
      });
    }
  };

  render() {
    return (
      <Router>
        <div>
          <HamburgerMenu show={!this.state.projectOpen} />
          <Route
            path="/"
            render={(props) => (
              <Home
                onProjectOpen={this.onProjectOpen}
                onProjectClose={this.onProjectClose}
                onShowMoreProjects={this.onShowMoreProjects}
                maxProjectsLength={this.state.availableProjects.length}
                {...props}
                {...this.state}
              />
            )}
          />
        </div>
      </Router>
    );
  }
}

export default App;



